import {NgModule} from '@angular/core';
import {RouterModule, Routes} from "@angular/router";

import {AdvPositionEditComponent} from './advPosition/adv-position-edit/adv-position-edit.component';
import {AdvPositionPendingComponent} from './advPosition/adv-position-pending/adv-position-pending.component';
import {AdvPositionToStartComponent} from './advPosition/adv-position-to-start/adv-position-to-start.component';
import {AdvPositionProcessingComponent} from './advPosition/adv-position-processing/adv-position-processing.component';
import {AdvPositionListComponent} from './advPosition/adv-position-list/adv-position-list.component';
import {ShareModule} from "../../shareModel/share.module";

import { HomeManagementService } from './home-management.service'




const AdvPositions = [
    AdvPositionEditComponent,
    AdvPositionPendingComponent,
    AdvPositionToStartComponent,
    AdvPositionProcessingComponent,
    AdvPositionListComponent
];

const HOME_MANAGEMENT_ROUTES: Routes = [
    {
        path: 'homeManagement/advPosition/advPosition-edit',
        component: AdvPositionEditComponent
    },
    {
        path: 'homeManagement/advPosition/advPosition-list',
        component: AdvPositionListComponent
    },
    {
        path: 'homeManagement/advPosition/advPosition-pending',
        component: AdvPositionPendingComponent
    },
    {
        path: 'homeManagement/advPosition/advPosition-processing',
        component: AdvPositionProcessingComponent
    },
    {
        path: 'homeManagement/advPosition/advPosition-to-start',
        component: AdvPositionToStartComponent
    },
];

@NgModule({
    imports: [
        RouterModule.forChild(HOME_MANAGEMENT_ROUTES),
        ShareModule
    ],
    declarations: [...AdvPositions],
    providers: [HomeManagementService],
    exports: [
        ...AdvPositions,
        RouterModule
    ],
})
export class HomeManagementModule {
}
